import React from 'react';
import { Link } from 'react-router-dom';

function Header(){
    const headerStyle = {
        background: '#333',
        color: '#fff',
        textAlign : 'center',
        padding: '10px'
      }
    
    const link = {
        color: 'white'
    }
    return (
        <header style={headerStyle}>
            <h1>Todo List</h1>
            <Link style={link} to="/">Home</Link> | <Link style={link} to="/about">About</Link>
        </header>
    )
}


export default Header;


